<script lang="ts">
	import { onMount } from "svelte";
	import firebase from "firebase";
	import { auth, googleProvider, db } from "./firebase";
	import List from "./List.svelte";
	import AddList from "./AddList.svelte";

	interface List {
		listId: string;
		todos: Array<{ name: string; completed: boolean }>;
	}

	let currentUser = null;
	let lists: Array<List> = [];

	onMount(() => {
		firebase.auth().onAuthStateChanged(async (user) => {
			if (user) {
				currentUser = user;
				db.collection("lists")
					.where("userId", "==", user.uid)
					.where("shared", "array-contains", user.uid)
					.onSnapshot((snap) => {
						lists = snap.docs.map(
							(doc) => ({ listId: doc.id, ...doc.data() } as List)
						);
						console.log(
							"DATA: ",

							lists
						);
					});
				console.log("User id: ", user.uid);
			} else {
				currentUser = null;
			}
		});
	});

	function login() {
		auth.signInWithPopup(googleProvider);
	}
</script>

<style global lang="postcss">
	/* only apply purgecss on utilities, per Tailwind docs */
	/* purgecss start ignore */
	@tailwind base;
	@tailwind components;
	/* purgecss end ignore */

	@tailwind utilities;

	body {
		margin: 0;
		padding: 0;
	}
</style>

{#if currentUser}
	<div class="h-screen bg-blue-600">
		<div class="p-5 flex space-x-8">
			{#each lists as list}
				<List listId={list.listId} />
			{/each}
			<div>
				<AddList />
			</div>
		</div>
	</div>
{:else}Not logged in <button on:click={() => login()}>Login</button>{/if}
