<script lang="ts">
	import firebase from "firebase";
	import { db } from "./firebase";
	import { v4 as uuid } from "uuid";

	export let listId;

	let hoverListName = false;

	interface List {
		id: string;
		name: string;
		todos: Array<Todo>;
	}

	interface Todo {
		id: string;
		completed: boolean;
		name: string;
	}

	let newTodo = "";
	let list: List = null;

	db.collection("lists")
		.doc(listId)
		.onSnapshot((data) => {
			list = {
				id: data.id,
				name: data.data().name,
				todos: data.data().todos,
			};
		});

	$: console.log("TODOS: ", list);
	$: reversedTodos = list ? [...list.todos].reverse() : [];

	function addItem(e) {
		e.preventDefault();
		if (newTodo.trim().length > 0) {
			let temp = {
				id: uuid(),
				name: newTodo,
				completed: false,
				// createdAt: firebase.firestore.FieldValue.serverTimestamp(),
				// updatedAt: firebase.firestore.FieldValue.serverTimestamp(),
			};

			db.collection("lists")
				.doc(listId)
				.update({
					todos: [...list.todos, temp],
				});
			newTodo = "";
		}
	}

	function toggle(item) {
		let updatedTodos = list.todos.map((todo) => {
			if (item.id === todo.id) {
				todo.completed = !todo.completed;
			}
			return todo;
		});
		db.collection("lists").doc(listId).update({ todos: updatedTodos });
	}
</script>

<style>
	.completed {
		text-decoration: line-through;
	}
</style>

<!-- <button on:click={()=> todos = todos.filter(t => !t.completed)}>Clear completed</button> -->

<div class="p-5 w-80 text-gray-900 bg-gray-200 rounded-md">
	<h3
		on:mouseenter={() => (hoverListName = true)}
		on:mouseleave={() => (hoverListName = false)}
		class="flex font-bold items-center relative text-lg">
		{list && list.name}
		{#if hoverListName}
			<span class="absolute w-5 h-5 right-0 text-gray-600"><svg
					xmlns="http://www.w3.org/2000/svg"
					viewBox="0 0 20 20"
					fill="currentColor">
					<path
						d="M17.414 2.586a2 2 0 00-2.828 0L7 10.172V13h2.828l7.586-7.586a2 2 0 000-2.828z" />
					<path
						fill-rule="evenodd"
						d="M2 6a2 2 0 012-2h4a1 1 0 010 2H4v10h10v-4a1 1 0 112 0v4a2 2 0 01-2 2H4a2 2 0 01-2-2V6z"
						clip-rule="evenodd" />
				</svg></span>
		{/if}
	</h3>
	<ul class="pt-3 space-y-2">
		{#each reversedTodos as item}
			<li
				class="p-2 bg-white rounded-md border-b-2 border-gray-300"
				class:completed={item.completed}
				style="text-decoration: {item.completed ? 'line-through' : ''}"
				on:click={() => toggle(item)}>
				{item.name}
			</li>
		{/each}
	</ul>

	<hr class="my-4 border-t border-gray-400" />

	<form class="" on:submit={(e) => addItem(e)}>
		<input
			bind:value={newTodo}
			class="p-2 w-full bg-white rounded-md border-0 border-b-2 border-gray-300"
			placeholder={'What to do?'} />
	</form>
</div>
